.wcf--a-accordion {
  text-align: start;
  display: flex;
  flex-direction: column;

  svg {
    width: 1em;
    height: 1em
  }

  .accordion-item {
    border: 1px solid #d5d8dc;

    &.element-active .icon-opened{
      display: block;
    }
    &.element-active .icon-closed{
      display: none;
    }
  }

  //.accordion-item + .accordion-item {
  //  margin-top: -1px;
  //}

  .tab-title {
    margin: 0;
    padding: 15px 20px;
    font-weight: 700;
    line-height: 1;
    cursor: pointer;
    outline: none
  }

  .accordion-icon {
    display: inline-block;
    width: 1.5em;
    &.accordion-icon-right {
      float: right;
      text-align: right
    }

    &.accordion-icon-left {
      float: left;
      text-align: left
    }
  }

  .tab-content {
    display: none;
    padding: 15px 20px;
    border-top: 1px solid #d5d8dc
  }

  .icon-opened{
    display: none;
  }
}
